        <b-alert variant="danger" v-if="errorfind" show>email must be valid</b-alert>
      </b-col>
    </b-row>
    <div v-if="detailTrx">
      <b-col cols="12">
        <b-row>
          <b-col class="flex-grow-1 overflow-hidden">
            <b-row>
              <b-col>
                <h3>Details Transaksi :</h3>
                <h5>Date : {{ new Date(details.created_at).toLocaleString('id-ID', { timeZone: 'Asia/Jakarta' }) }}</h5>
                <h5>Amount: {{ parseFloat(details.amount).toLocaleString() }} SPC</h5>
                <h5>Email : {{ details.user_email }}</h5>
                <h5>
                  State : <span v-if="(details.state = 'collectin')"> collected </span>
                  <span v-else> {{ details.state }} </span>
                </h5>
                <h5 v-if="details.to">To Address : {{ details.to }}</h5>
                <h5 v-if="details.tx_wd">Trx WD : {{ details.tx_wd }}</h5>
                <h5 v-if="details.tx_deposit">Trx Depo : {{ details.tx_deposit }}</h5>
                <h5 v-if="details.tx_fee">Trx Fee : {{ details.tx_fee }}</h5>
                <h5 v-if="details.tx_collect">Trx Collect : {{ details.tx_collect }}</h5>
              </b-col>
            </b-row>
          </b-col>
        </b-row>
      </b-col>
      <b-col>
        <span class="mb-0 text-black-80 hp-text-color-dark-30">
          <b-button variant="primary" class="btn-ghost" @click="setDetails(false)"> Back </b-button>
        </span>
      </b-col>
    </div>
    <div v-else>
      <div v-if="tradeData.length === 0">No Data To Display</div>
      <div v-else>
        <b-table-simple responsive borderless class="mb-0">
          <b-thead>
            <b-tr>
              <b-th>
                <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> # </span>
              </b-th>
              <b-th>
                <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> Balance Id </span>
              </b-th>
              <b-th>
                <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> Type </span>
              </b-th>
              <b-th>
                <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> amount </span>
              </b-th>
              <b-th>
                <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> State </span>
              </b-th>
              <b-th>
                <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> details </span>
              </b-th>
