<script>
import { BAlert, BPagination, BFormInput, BLink, BFormSelect, BButton, BRow, BCol, BCard, BBadge, BTableSimple, BThead, BTbody, BTr, BTh, BTd } from 'bootstrap-vue';
import { mapActions, mapGetters, mapState } from 'vuex';

export default {
  computed: {
    ...mapGetters('depoWdStore', ['tradeData', 'typeData', 'detailTrx']),
    filterStates() {
      return this.states.filter((item) => {
        return item.title.toLowerCase().includes(this.state.toLowerCase());
      });
    },
  },
  data() {
    return {
      tx_id: '',
      selected: 'deposit',
      modal: false,
      details: {},
      states: 'searchData',
      options: [
        { value: 'deposit', text: 'deposit' },
        { value: 'withdrawal', text: 'withdrawal' },
      ],
      errorfind: false,
    };
  },
  watch: {
    selected(newValue, oldValue) {
      if (newValue == 'deposit') {
        this.getList('deposit');
      }
      if (newValue == 'withdrawal') {
        this.getList('withdrawal');
      }
    },
  },
  methods: {
    seeDetails(_data) {
      this.setDetails(true);
      this.details = _data;
    },
    ...mapActions('depoWdStore', ['getList', 'findUsers', 'setDetails', 'getPrevData', 'getNextData']),
    async findHash() {
      const regex = /^0x([0-9a-fA-F]{64})$/;
      if (!regex.test(this.hash)) {
        this.errorfind = true;
        return;
      }
      this.errorfind = false;
      await this.findUsers(this.email);
      console.log(this._email);
    },

    async prevPage() {
      console.log('find user clicked');
      await this.getPrevData();
    },
    async nextPage() {
      console.log('find user clicked');
      await this.getNextData();
    },
  },

  components: {
    BRow,
    BCol,
    BCard,
    BBadge,
    BTableSimple,
    BThead,
    BTbody,
    BTr,
    BTh,
    BTd,
    BButton,
    BFormSelect,
    BFormInput,
    BLink,
    BPagination,
    BAlert,
  },
};
</script>
